@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: bold;
}

:root {
  --shadow-of-text: 0 4px 4px #00000040;
  --dark-shadow-box: 0 4px 4px 0 #00000040;
}

.big__form, .small__form {
  margin: 20px 0 0;
}

.logo {
  position: absolute;
}

.big__logo {
  width: 19px;
  height: 19px;
  margin: 26px 0 25px 26px;
}

.small__logo {
  width: 11px;
  margin: 15px 0 16px 13px;
}

.input {
  color: #3d4e61;
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: Avenir, sans-serif;
  font-weight: normal;
}

.input::placeholder {
  color: #3d4e61;
  font-weight: normal;
}

.big__search {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.big__search:hover {
  box-shadow: var(--dark-shadow-box);
}

.big__search:focus {
  box-shadow: var(--dark-shadow-box);
  text-shadow: var(--shadow-of-text);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  outline: none;
  font-weight: bold;
}

.small__search {
  text-shadow: var(--shadow-of-text);
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.small__search:hover {
  text-shadow: none;
  box-shadow: 0 3px 8px #3d4e6133;
}

.small__search:focus {
  text-shadow: none;
  background: linear-gradient(#fff, #f6f6f7);
  outline: none;
  font-weight: bold;
}

